import styled from "styled-components";

function Header() {
    return (
        <div>
            <Hide>
                <SocialMediaMenu>
                    <SocialMedia>
                        <Item><ItemLink href="https://github.com/MKAbuMattar"><Github /></ItemLink></Item>
                        <Item><ItemLink href="https://www.linkedin.com/in/mkabumattar/"><LinkedIn /></ItemLink></Item>
                        <Item><ItemLink href="https://codepen.io/MKAbuMattar"><Codepen /></ItemLink></Item>
                        <Item><ItemLink href="https://www.instagram.com/mkabumattar/"><Instagram /></ItemLink></Item>
                        <Item><ItemLink href="https://twitter.com/mkabumattar"><Twitter /></ItemLink></Item>
                    </SocialMedia>
                </SocialMediaMenu>
                <Email>
                    <Mail>
                        <a href="mailto:mohammad.khaled@outlook.com">mohammad.khaled@outlook.com</a>
                    </Mail>
                </Email>
            </Hide>
            <Head>
                <Width>
                    <Text1>Hi, my name is</Text1>
                    <MyName>Mohammad Abu Mattar</MyName>
                    <Text2>I build things for the web.</Text2>
                    <Text3>I'm a software engineer based in Amman, exceptional websites developer (and occasionally designing), and everything in between.</Text3>
                    <Btn href="/">Get In Touch</Btn>
                    <BtnAbout><a href="#about"><About></About></a></BtnAbout>
                </Width>
            </Head >
        </div>
    );
}

export default Header;

const Hide = styled.div`
    @media (max-width: 799px) {
        display: none;
    }
`;

const SocialMediaMenu = styled.div`
    width: 40px;
    position: fixed;
    bottom: 0px;
    left: 40px;
    right: auto;
    z-index: 10;
    color: var(--secondary);

    @media (max-width: 1080px) {
        left: 20px;
    }
    
    @media (max-width: 800px) {
        left: 15px;
    }
`;

const SocialMedia = styled.ul`
    display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    margin: 0px;
    padding: 0px;
    list-style: none;

    &::after {
        content: "";
        display: block;
        width: 1px;
        height: 90px;
        margin: 0px auto;
        background-color: var(--secondary);
    }
`;

const Item = styled.li`
    padding: 10px;
`;

const ItemLink = styled.a`
    
`;

