const Email = styled.div`
    width: 40px;
    position: fixed;
    bottom: 0px;
    left: auto;
    right: 40px;
    z-index: 10;
    color: var(--primary);

    &::after {
        content: "";
        display: block;
        width: 1px;
        height: 90px;
        margin: 0px auto;
        background-color: var(--secondary);
    }

    &:hover{
        color: var(--secondary);
    }

    @media (max-width: 1080px) {
        right: 20px;
    }

    @media (max-width: 800px) {
        right: 15px;
    }
`;

const Mail = styled.div`
    display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    position: relative;

    & a{
        margin: 20px auto;
        padding: 10px;
        letter-spacing: 0.1em;
        -webkit-writing-mode: vertical-rl;
        writing-mode: vertical-rl;
        display: inline-block;
        text-decoration: none;
        text-decoration-skip-ink: auto;
        color: inherit;
        position: relative;
        cursor: pointer;
    }
`;

const Head = styled.header`
    color: var(--shades);
    height: 100vh;
    position: relative;
    background-color: var(--primary);
`;

const Width = styled.div`
    width: 60%;
    margin: 0 auto;
    padding-top: 15%;

    @media (max-width: 799px) {
        width: 85%;
        padding-top: 30%;
    }
`;

const Text1 = styled.div`
    color: var(--secondary);
    font-size: 1.2rem;
    @media (max-width: 799px) {
        font-size: 1rem;
    }
`;

const MyName = styled.h1`
    font-size: 3rem;
    margin-block-start: 0em;
    margin-block-end: 0em;
    @media (max-width: 799px) {
        font-size: 1.9rem;
    }
`;

const Text2 = styled.div`
    color: var(--shades__of__silver);
    font-size: 3rem;
    @media (max-width: 799px) {
        font-size: 1.9rem;
    }
`;

const Text3 = styled.p`
    color: var(--shades__of__silver);
    max-width: 500px;
`;

